import Vue from 'vue'
import Router from 'vue-router'
/* Layout */
import Layout from '@/layout'

Vue.use(Router)

export const constantRoutes = [
  {
    path: '/redirect',
    component: () => import('../views/Home'),
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('../views/redirect/index')
      }
    ]
  },
  {
    path: '/login',
    component: Layout,
    redirect: '/login',
    children: [
      {
        path: '/',
        component: () => import('../views/login/index'),
        hidden: true,
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/auth-redirect',
    component: () => import('../views/login/auth-redirect'),
    hidden: true,
    meta: { requireAuth: false },
  },
  {
    path: '/',
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: '/',
        component: () => import('../views/home/index'),
        name: 'home',
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/gotravel',
    component: Layout,
    meta: { requireAuth: false },
    redirect: '/gotravel',
    children: [
      {
        path: '/',
        component: () => import('../views/gotravel/index'),
        name: 'gotravel',
        meta: { requireAuth: true }, // 暫時關閉
      },
      {
        path: 'tripmap',
        component: () => import('../views/gotravel/tripMap'),
        name: 'tripmap',
        meta: { requireAuth: false },  // 暫時關閉
      }
    ]
  },
  {
    path: '/travel',
    component: Layout,
    redirect: '/travel',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "travel" */ '../views/Travel.vue'),
        name: 'travel',
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/user',
    component: Layout,
    redirect: '/user',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "user" */ '../views/user/index'),
        name: 'user',
        meta: { requireAuth: true },
      }
    ]
  },
  {
    path: '/usertravel',
    component: Layout,
    redirect: '/user/travel/:id',
    children: [
      {
        path: '/',
        name: 'usertravel',
        component: () => import('../views/user/components/UserTravel'),
        meta: { requireAuth: true },
      }
    ]
  },
  {
    path: '/place',
    component: Layout,
    redirect: '/place',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "place" */ '../views/Place.vue'),
        name: 'place',
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/about',
    component: Layout,
    redirect: '/about',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
        name: 'about',
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/placedetail',
    component: Layout,
    redirect: '/placedetail/:id',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "placedetail" */ '../views/PlaceDetail.vue'),
        name: 'placedetail',
        meta: { requireAuth: false },
      }
    ]
  },
  {
    path: '/searchresults',
    component: Layout,
    redirect: '/searchresults/:value',
    children: [
      {
        path: '/',
        component: () => import(/* webpackChunkName: "searchresults" */ '../views/SearchResults.vue'),
        name: 'searchresults',
        meta: { requireAuth: false },
      }
    ]
  },
]

/**
 * asyncRoutes
 * the routes that need to be dynamically loaded based on user roles
 */
export const asyncRoutes = [
  // 404 page must be placed at the end !!!
  { path: '*', redirect: '/404', hidden: true }
]

const createRouter = () => new Router({
  mode: 'history', // require service support
  base: process.env.BASE_URL,
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})

const router = createRouter()

// Detail see: https://github.com/vuejs/vue-router/issues/1234#issuecomment-357941465
export function resetRouter() {
  const newRouter = createRouter()
  router.matcher = newRouter.matcher // reset router
}

export default router
