import { createTrip } from '@/api/trip'
import store from '@/store'

const state = {
  list: []
}

const actions = {
    createTrip(_, data) {
    return new Promise((resolve, reject) => {
        createTrip(store.getters.token, data).then(response => {
        resolve(response)
      }).catch(error => {
        reject(error)
      })
    })
  }
}

const mutations = {
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
